                  {{item.anwserquestionNum}}
                </td>
                <td v-cloak v-if="commentStatus == 'uncomment'">
                  {{item.noncommentNum}}
                </td>
                <td v-cloak>
                  <span style="cursor: pointer;color: #6612CF" @click="gotoProjectDetail(item)">查看</span>
                </td>
              </tr>
              </tbody>
